const divvyContract = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
const deadWallet = '0x000000000000000000000000000000000000dead';
const fBNBContract = '0x87b1AccE6a1958E522233A737313C086551a5c76';

const contract = new web3.eth.Contract(smartdefi_abi, divvyContract)
const fBNBcontract = new web3.eth.Contract(abi, fBNBContract)

const NumberFormatter = new Intl.NumberFormat('en-US', {
    maximumSignificantDigits: 5
});

function DivvyTokenStats() {

    const [burnt, setBrunt] = useState(0);
    const [liquidity, setLiquidity] = useState(0);
    const [fbnb, setFbnb] = useState(0);
    const [supply, setSupply] = useState(0);
    const [backing, setBacking] = useState(0);
    const [backingPerToken, setBackingPerToken] = useState(0);

    useEffect(() => {

        //balance of fBNB in Divvy
        fBNBcontract.methods.balanceOf(divvyContract).call((err, resp) => {
            if (!err) {
                setFbnb(resp / Math.pow(10, 18))
            }
        })

        //balance of divvy in Liquidity
        contract.methods.getBalance(divvyContract).call((err, resp) => {
            if (!err) {
                setLiquidity(resp / Math.pow(10, 9))
            }
        })

        // contract.methods.balanceOf(divvyContract).call((err, resp) => {
        //     if (!err) {
        //         setLiquidity(resp / Math.pow(10, 9))
        //     }
        // })

        //balance of divvy in dead wallet
        contract.methods.balanceOf(deadWallet).call((err, resp) => {
            if (!err) {
                setBrunt(resp / Math.pow(10, 9))
            }
        })

        //total supply
        contract.methods.totalSupply().call((err, resp) => {
            if (!err) {
                setSupply(resp / Math.pow(10, 9))
            }
        })

        //asset backing
        contract.methods._totalSupply5().call((err, resp) => {
            if (!err) {
                setBacking(resp / Math.pow(10, 18))
            }
        })

        //backing per token
        contract.methods.oneTokentoBacking().call((err, resp) => {
            if (!err) {
                setBackingPerToken(resp / Math.pow(10, 18))
            }
        })

        // contract.methods.Backing().call((err, resp) => {
        //     //TODO, dont know what this is
        //     // console.log('Backing', resp, err)
        // })

        contract.methods._totalSupply1().call((err, resp) => {
            console.log('_totalSupply1', resp / Math.pow(10, 9), err)
        })

        contract.methods._totalSupply2().call((err, resp) => {
            console.log('_totalSupply2', resp / Math.pow(10, 9), err)
        })

    }, [])

    return (<Grid container>
        <Grid item xs={12} md={6} >
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><AccountBalanceIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(supply) + " DIVVY"} secondary="Total Supply" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><LocalFireDepartmentIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(burnt) + " DIVVY"} secondary="Burnt Supply" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><CachedIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(supply - burnt) + " DIVVY"} secondary="Circulating Supply" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><LocalOfferIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format((fbnb - backing)/liquidity) + " BNB"} secondary="Price" />
                </ListItem>
            </List>
        </Grid>
        <Grid item xs={12} md={6} >
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><AttachMoneyIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(liquidity) + " DIVVY"} secondary="Liquidity Divvy" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><AttachMoneyIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(fbnb - backing) + " fBNB"} secondary="Liquidity fBNB" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><ShieldIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(backing) + " fBNB"} secondary="Total Backing" />
                </ListItem>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar sx={{ bgcolor: '#872eff' }}><ShieldIcon /></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={NumberFormatter.format(backingPerToken) + " fBNB"} secondary="Backing Per Token" />
                </ListItem>
            </List>
        </Grid>
    </Grid>)

}

export default DivvyTokenStats;